import axios from 'axios'

const state = {
    user: null,
    devices: null
}

const getters = {
    isAuthenticated: state => !!state.user,
    StateUser: state => state.user,
    StateDevices: state => state.devices
}

const actions = {
    async LogIn({ commit }, User) {
        await axios.post('session/', JSON.stringify(Object.fromEntries(User)), {
                withCredentials: true
            })
            .then(async response => {
                await commit('setUser', response.data)
            })
    },
    async LogOut({ commit }) {
        let user = null
        commit('logout', user)
    },
    async GetDevices({ commit }) {
        await axios.get('device/', {
                withCredentials: true
            })
            .then(async response => {
                await commit('setDevices', response.data)
            })
            .catch(error => {
                console.log(error)
            })
    }
}

const mutations = {
    setUser(state, user) {
        state.user = user
    },
    setDevices(state, devices) {
        state.devices = devices
    },
    logout(state) {
        state.user = null
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}